<template>
  <div class="order">
    <mu-tabs :value="activeTab" @change="handleTabChange">
      <mu-tab :value="1" title="全部订单"/>
      <mu-tab :value="2" title="待评价"/>
    </mu-tabs>
    <ordercard :order="orderList" @click="getOrder"></ordercard>
    <evaluate :evalShow="evalShow" @evalBack="closeEval" :evalObj="evalObj" @submit="handleSubmit"></evaluate>
  </div>
</template>
<script>
import { ordercard, evaluate } from "@/components";
import Util from "@/libs/util";
import Cookies from "js-cookie";
export default {
  data() {
    return {
      activeTab: 1,
      orderList: [],
      orderCache: [],
      evalShow: false,
      evalObj: null
    };
  },
  components: {
    ordercard,
    evaluate
  },
  methods: {
    handleSubmit(val){
      Util.ajax.post('/api/client/user/evaluate',Object.assign({},this.evalObj,val)).then(res=>{
        if(res.data.status===1){
          this.initData();
          alert('评论成功');
          this.evalShow = false;
        }
      })

    },
    closeEval() {
      this.evalShow = false;
    },
    getOrder(val) {
      this.evalObj = this.orderList[val];
      this.evalShow = true;
    },
    handleTabChange(val) {
      switch (val) {
        case 1:
          this.orderList = Object.assign([], this.orderCache);
          break;
        case 2:
          this.orderList = Object.assign([], this.orderCache).filter(
            cur => cur.eval === 0
          );
          break;
      }
      this.activeTab = val;
    },
    initData() {
      Util.ajax
        .post("/api/client/user/order", {
          userId: Cookies.get("userId")
        })
        .then(res => {
          if (res.data.status === 1) {
            this.orderList = res.data.data;
            this.orderCache = res.data.data;
          }
        });
    }
  },
  mounted() {
    this.initData();
  }
};
</script>
<style lang="scss" scoped>
.order {
  //margin:48px auto 56px;
  .orderStatus {
    font-size: 17px;
    line-height: 17px;
    font-weight: 500;
  }
}

.mu-tabs {
  position: fixed;
  top: 0px;
}
</style>



